.container {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.card {
  border-radius: 10px;
  width: 25%;
  box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.3);
  padding: 15px;
  background-color: black;
  margin: 1rem 2rem;

  img {
    border-radius: 10px;
    width: 100%;
    height: 400px;
    margin-top: 10px;
    -webkit-filter: grayscale(0%);
    filter: grayscale(0%);
    opacity: 1;
  }

  .image_desc {
    position: relative;
  }
  .image_desc:hover .desc {
    display: block;
    background-color: black;
  }
  .image_desc:hover .img {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: 0.9;
  }

  .desc {
    position: absolute;
    bottom: 5px;
    display: none;
    color: white;
  }

  h1 {
    text-align: left;
    color: white;
    font-family: "Amatic SC", cursive;
  }
}
